<template>
  <v-dialog v-model="dialogExport" persistent max-width="600px">
    <template v-slot:activator="{ on, attrs }">
      <v-btn class="mt-3" color="success" block v-bind="attrs" v-on="on">
        <b>Descarga Excel</b>
        <v-icon right> mdi-file-excel </v-icon>
      </v-btn>
    </template>
    <v-card class="rounded-lg">
      <v-card-title class="primary">
        <span class="text-h5 white--text">Descarga Excel</span>
      </v-card-title>
      <v-card-text class="mt-5">
        <v-container>
          <v-form>
            <v-text-field
              v-model="startDate"
              label="Fecha de inicio"
              type="date"
              outlined
              clearable
              required
            ></v-text-field>
            <v-text-field
              v-model="endDate"
              label="Fecha de fin"
              type="date"
              outlined
              clearable
              required
            ></v-text-field>
          </v-form>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="error" text @click="dialogExport = false">
          <b>Cerrar</b>
        </v-btn>
        <v-btn
          color="success"
          text
          @click="exportExcel()"
          :disabled="startDate == null || endDate == null"
          ><b>Descargar</b>
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
    
<script>
import { utils, write } from "xlsx";
import { saveAs } from "file-saver";
export default {
  name: "FinanceExportExcel",
  data() {
    return {
      dialogExport: false,
      startDate: null,
      endDate: null,
    };
  },
  created() {},
  methods: {
    async exportExcel() {
      await this.$axios
        .get(
          "/report/pays-csv?startDate=" +
            this.startDate +
            "&endDate=" +
            this.endDate,
          {
            headers: {
              Authorization: `Bearer ${localStorage.token}`,
            },
          }
        )
        .then((response) => {
          const wb = utils.book_new();
          const ws = utils.json_to_sheet(response.data);

          ws["A1"].v = "PATERNO";
          ws["B1"].v = "MATERNO";
          ws["C1"].v = "NOMBRES";
          ws["D1"].v = "CUI";
          ws["E1"].v = "CONCEPTO";
          ws["F1"].v = "MONTO";
          ws["G1"].v = "NUMERO OPERACION";
          ws["H1"].v = "FECHA DE PAGO";
          ws["I1"].v = "CORREO";
          ws["J1"].v = "ESPECIALIDAD";

          utils.book_append_sheet(wb, ws, "Pagos");

          const wbout = write(wb, { bookType: "xlsx", type: "array" });
          const blob = new Blob([wbout], { type: "application/octet-stream" });
          saveAs(
            blob,
            "Relación de Pagos " +
              this.startDate +
              " al " +
              this.endDate +
              ".xlsx"
          );

          this.dialogExport = false;
        })
        .catch((error) => {
          this.$queryError(error);
        });
    },
  },
};
</script>